@charset "UTF-8";
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before, blockquote:after {
  content: "";
  content: none;
}

q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

img {
  font-size: 14px;
  font-weight: 700;
}

.float-left {
  float: left;
}

.mb-md {
  margin-bottom: 24px;
}

.mr-sm {
  margin-right: 12px;
}

.float-right {
  float: right;
}

.clearfix::after {
  content: "";
  clear: both;
  display: table;
}

.correct::before {
  content: "✔";
  position: relative;
  left: -5px;
  color: green;
}

.incorrect::before {
  content: "⨯";
  position: relative;
  left: -5px;
  color: red;
}

.col {
  width: calc((100% - 24px) / 2);
}
.col:not(:last-child) {
  margin-right: 24px;
}

body {
  font-family: Helvetica, sans-serif;
}

.txt-xl {
  font-size: 41px;
  letter-spacing: 0;
  color: #646464;
}
@media only screen and (max-width: 960px) {
  .txt-xl {
    font-size: 30px;
  }
}

.txt-lg {
  font-size: 24px;
  letter-spacing: 0;
  color: #949494;
}
@media only screen and (max-width: 960px) {
  .txt-lg {
    font-size: 20px;
  }
}

.txt-md {
  font-size: 21px;
  letter-spacing: 0.5px;
  color: #0b0b0b;
}
@media only screen and (max-width: 960px) {
  .txt-md {
    font-size: 18px;
  }
}
@media only screen and (max-width: 768px) {
  .txt-md {
    font-size: 16px;
  }
}
@media only screen and (max-width: 600px) {
  .txt-md {
    font-size: 14px;
  }
}

.txt-sm {
  font-size: 14px;
  letter-spacing: 0.5px;
}
@media only screen and (max-width: 960px) {
  .txt-sm {
    font-size: 12px;
  }
}
@media only screen and (max-width: 425px) {
  .txt-sm {
    font-size: 10px;
  }
}

.txt-xs {
  font-size: 12px;
  letter-spacing: 0.1px;
  color: #949494;
}
@media only screen and (max-width: 960px) {
  .txt-xs {
    font-size: 10px;
  }
}

body {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(to top, #7826c6, #678aff);
  position: relative;
}

.success-popup {
  background: #fff;
  border-radius: 5px;
  position: absolute;
  bottom: 0;
  left: 50%;
  display: none;
  transform: translateX(-50%) scale(0);
  visibility: hidden;
  box-shadow: 0 8px 14px 0 rgba(0, 0, 0, 0.3);
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 24px 48px;
  transition: all 0.3s ease-in;
  z-index: 1;
}
.success-popup.active {
  bottom: 50%;
  transform: translateX(-50%) scale(1);
  visibility: visible;
}
.success-popup h3,
.success-popup p {
  margin-bottom: 12px;
}
.success-popup h3 {
  font-size: 16px;
  font-weight: 700;
  color: #4ce052;
  text-transform: uppercase;
}
.success-popup img {
  display: inline-block;
  margin-bottom: 12px;
}
.success-popup button {
  padding: 12px 24px;
  border: none;
  background: #3766ff;
  color: #fff;
  border-radius: 4px;
}

.container {
  background: #fff;
  border-radius: 6px;
  box-shadow: 0 8px 14px 0 rgba(0, 0, 0, 0.07);
  width: 50%;
  max-width: 576px;
  padding: 72px 12px 84px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media only screen and (max-width: 1280px) {
  .container {
    width: 60%;
  }
}
@media only screen and (max-width: 960px) {
  .container {
    width: 70%;
    padding: 54px 12px 72px;
  }
}
@media only screen and (max-width: 768px) {
  .container {
    width: 70%;
    padding: 36px 12px 60px;
  }
}
@media only screen and (max-width: 600px) {
  .container {
    width: 90%;
    padding: 36px 12px 48px;
  }
}
.container h1 {
  text-transform: capitalize;
}

form {
  width: 60%;
  padding: 48px 0;
}
@media only screen and (max-width: 768px) {
  form {
    width: 70%;
  }
}
@media only screen and (max-width: 600px) {
  form {
    width: 80%;
    padding: 36px 0;
  }
}
@media only screen and (max-width: 425px) {
  form {
    width: 90%;
  }
}
form .btn {
  width: 100%;
  background: #3766ff;
  text-transform: capitalize;
  font-size: 24px;
  color: #fff;
  border: none;
  border-radius: 2px;
  padding: 18px 0;
}
@media only screen and (max-width: 768px) {
  form .btn {
    font-size: 20px;
    padding: 12px 0;
  }
}

.input-grp {
  width: 100%;
  border: 1px solid rgba(151, 151, 151, 0.76);
  border-radius: 2px;
  padding: 24px 8px 12px;
  position: relative;
}
.input-grp:not(:last-child) {
  margin-bottom: 24px;
}
@media only screen and (max-width: 768px) {
  .input-grp {
    padding: 12px 6px 6px;
  }
}
.input-grp.error {
  border: 1px solid rgba(255, 0, 0, 0.76);
}
.input-grp.error small {
  visibility: visible;
  opacity: 1;
}
.input-grp.success {
  border: 1px solid #4ce052;
}
.input-grp small {
  position: absolute;
  bottom: -12px;
  left: 0;
  color: red;
  font-size: 10px;
  visibility: hidden;
  opacity: 0;
  transition: all 0.25s ease-in;
}
.input-grp label {
  display: inline-block;
  position: absolute;
  left: 8px;
  transform: translateY(-50%);
  top: 10px;
  color: #678aff;
  transition: all 0.25s ease;
}
.input-grp input {
  width: 78%;
  border: none;
  outline: none;
}
.input-grp .toggle {
  position: absolute;
  right: 0;
  top: 0;
  width: 22%;
  height: 100%;
  background: #f0f0f0;
  color: #678aff;
  text-transform: uppercase;
  border: none;
  border-left: 1px solid #979797;
  cursor: pointer;
}

.pass__strength {
  margin-top: 12px;
}
.pass__strength-parameter {
  display: inline-block;
}
.pass__strength-parameter .icon {
  color: #4ce052;
  font-size: 16px;
}
@media only screen and (max-width: 960px) {
  .pass__strength-parameter {
    font-size: 10px;
  }
}

footer {
  width: 100%;
  text-align: center;
  padding-top: 48px;
  border-top: 2px solid rgba(151, 151, 151, 0.185);
}
footer p {
  font-size: 18px;
  color: #636363;
}
footer p a {
  color: #678aff;
}
@media only screen and (max-width: 960px) {
  footer p {
    font-size: 16px;
  }
}
@media only screen and (max-width: 768px) {
  footer p {
    font-size: 14px;
  }
}
@media only screen and (max-width: 768px) {
  footer {
    padding-top: 36px;
  }
}