* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

a {
  text-decoration: none;
}
ul {
  list-style: none;
}

img {
  font-size: 14px;
  font-weight: 700;
}

.float-left {
  float: left;
}
.mb-md {
  margin-bottom: $margin-md;
}
.mr-sm {
  margin-right: $margin-sm;
}

.float-right {
  float: right;
}

.clearfix::after {
  content: "";
  clear: both;
  display: table;
}

.correct {
  &::before {
    content: "\2714";
    position: relative;
    left: -5px;
    color: green;
  }
}
.incorrect {
  &::before {
    content: "\2A2F";
    position: relative;
    left: -5px;
    color: red;
  }
}

.col {
  width: calc((100% - $margin-md) / 2);
  &:not(:last-child) {
    margin-right: $margin-md;
  }
}
