body {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(to top, #7826c6, #678aff);
  position: relative;
}
.success-popup {
  background: #fff;
  border-radius: 5px;
  position: absolute;
  bottom: 0;
  left: 50%;
  display: none;
  transform: translateX(-50%) scale(0);
  visibility: hidden;
  box-shadow: 0 8px 14px 0 rgba(0, 0, 0, 0.3);
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: $padding-md $padding-xl;
  transition: all 0.3s ease-in;
  z-index: 1;

  &.active {
    bottom: 50%;
    transform: translateX(-50%) scale(1);
    visibility: visible;
  }

  h3,
  p {
    margin-bottom: $margin-sm;
  }
  h3 {
    font-size: 16px;
    font-weight: 700;
    color: #4ce052;
    text-transform: uppercase;
  }
  img {
    display: inline-block;
    margin-bottom: $margin-sm;
  }

  button {
    padding: $padding-sm $padding-md;
    border: none;
    background: #3766ff;
    color: #fff;
    border-radius: 4px;
  }
}

.container {
  background: #fff;
  border-radius: 6px;
  box-shadow: 0 8px 14px 0 rgba(0, 0, 0, 0.07);
  width: 50%;
  max-width: 576px;
  padding: calc(2 * $padding-lg) $padding-sm calc(3.5 * $padding-md);
  display: flex;
  flex-direction: column;
  align-items: center;

  @include respond(laptop) {
    width: 60%;
  }
  @include respond(tablet-md) {
    width: 70%;
    padding: calc(1.5 * $padding-lg) $padding-sm calc(3 * $padding-md);
  }
  @include respond(tablet-sm) {
    width: 70%;
    padding: $padding-lg $padding-sm calc(2.5 * $padding-md);
  }
  @include respond(phone) {
    width: 90%;
    padding: $padding-lg $padding-sm calc(2 * $padding-md);
  }

  h1 {
    text-transform: capitalize;
  }
}
form {
  width: 60%;
  padding: $padding-xl 0;

  @include respond(tablet-sm) {
    width: 70%;
  }
  @include respond(phone) {
    width: 80%;
    padding: $padding-lg 0;
  }
  @include respond(phone-sm) {
    width: 90%;
  }
  .btn {
    width: 100%;
    background: #3766ff;
    text-transform: capitalize;
    font-size: 24px;
    color: #fff;
    border: none;
    border-radius: 2px;
    padding: calc($padding-sm + 6px) 0;
    @include respond(tablet-sm) {
      font-size: 20px;
      padding: $padding-sm 0;
    }
  }
}

.input-grp {
  width: 100%;
  border: 1px solid rgba(151, 151, 151, 0.76);
  border-radius: 2px;
  padding: $padding-md 8px $padding-sm;
  position: relative;

  &:not(:last-child) {
    margin-bottom: $margin-md;
  }
  @include respond(tablet-sm) {
    padding: $padding-sm 6px 6px;
  }

  &.error {
    border: 1px solid rgba(255, 0, 0, 0.76);

    & small {
      visibility: visible;
      opacity: 1;
    }
  }

  &.success {
    border: 1px solid #4ce052;
  }

  small {
    position: absolute;
    bottom: -$margin-sm;
    left: 0;
    color: red;
    font-size: 10px;
    visibility: hidden;
    opacity: 0;
    transition: all 0.25s ease-in;
  }

  label {
    display: inline-block;
    position: absolute;
    // top: 50%;
    left: 8px;
    // color: #979797;
    transform: translateY(-50%);
    top: 10px;
    color: #678aff;
    transition: all 0.25s ease;
  }
  input {
    width: 78%;
    border: none;
    outline: none;

    // &:focus + label {
    //   top: 10px;
    //   color: #678aff;
    // }
  }
  .toggle {
    position: absolute;
    right: 0;
    top: 0;
    width: 22%;
    height: 100%;
    background: #f0f0f0;
    color: #678aff;
    text-transform: uppercase;
    border: none;
    border-left: 1px solid #979797;
    cursor: pointer;
  }
}
.pass__strength {
  margin-top: $margin-sm;
  &-parameter {
    display: inline-block;
    .icon {
      color: #4ce052;
      font-size: 16px;
    }
    @include respond(tablet-md) {
      font-size: 10px;
    }
  }
}

footer {
  width: 100%;
  text-align: center;
  padding-top: $padding-xl;
  border-top: 2px solid rgba(151, 151, 151, 0.185);
  p {
    font-size: 18px;
    color: #636363;
    a {
      color: #678aff;
    }

    @include respond(tablet-md) {
      font-size: 16px;
    }
    @include respond(tablet-sm) {
      font-size: 14px;
    }
  }
  @include respond(tablet-sm) {
    padding-top: $padding-lg;
  }
}
