body {
  font-family: Helvetica, sans-serif;
}
.txt-xl {
  font-size: 41px;
  letter-spacing: 0;
  color: #646464;
  @include respond(tablet-md) {
    font-size: 30px;
  }
}
.txt-lg {
  font-size: 24px;
  letter-spacing: 0;
  color: #949494;
  @include respond(tablet-md) {
    font-size: 20px;
  }
}
.txt-md {
  font-size: 21px;
  letter-spacing: 0.5px;
  color: #0b0b0b;
  @include respond(tablet-md) {
    font-size: 18px;
  }
  @include respond(tablet-sm) {
    font-size: 16px;
  }
  @include respond(phone) {
    font-size: 14px;
  }
}
.txt-sm {
  font-size: 14px;
  letter-spacing: 0.5px;
  @include respond(tablet-md) {
    font-size: 12px;
  }
  @include respond(phone-sm) {
    font-size: 10px;
  }
}
.txt-xs {
  font-size: 12px;
  letter-spacing: 0.1px;
  color: #949494;

  @include respond(tablet-md) {
    font-size: 10px;
  }
}
